html,
body {
  border: 0px;
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
  font-family: sans-serif;
}
body {
  background: #EFEEE3;
  color: #585753;
  font-family: "Helvetica Neue", sans-serif;
}
a {
  color: #585753;
}
h1 {
  margin: 10px 0px;
  padding: 2rem 1.8rem;
  font-family: "Helvetica Neue", sans-serif;
  font-size: 30px;
  font-weight: bold;
  line-height: 1.3;
}
h2 {
  margin: -17px 0px 17px 0px;
  font-size: 19px;
  text-align: center;
}
img {
  -ms-interpolation-mode: bicubic;
}
strong {
  font-weight: bold;
}
.wrapper {
  max-width: 1403px;
  margin: 0px auto;
  padding: 20px;
}
.full,
.half,
.thirds,
.card {
  display: inline-block;
  font-size: 18px;
  vertical-align: top;
}
.full {
  width: 100%;
}
.half {
  width: 48%;
  width: calc(50% - 10px);
}
.thirds {
  width: 33.33333334%;
}
.two-thirds {
  width: 66.66666666%;
}
.card {
  width: calc(50% - 10px);
}
.hide {
  display: none;
}
header {
  text-align: center;
}
footer {
  background: #3C3B39;
  margin-top: 70px;
}
@media screen and (max-width: 980px) {
.card,
  .half,
  .community .half {
    width: 100%;
}
}
@media screen and (max-width: 700px) {
.thirds {
    width: 100%;
}
}
@media screen and (max-width: 590px) {
header h1 {
    font-size: 2em;
}
}
@media screen and (max-width: 450px) {
.card-header span {
    display: block;
    text-align: right;
    float: none;
}
}
#app {
  margin-top: 60px;
  color: #2C3E50;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}



